import Emitter from '@baifendian/adhere-util-emitter';
import BfdUtil from '@baifendian/adherev-util';
var withVue = BfdUtil._util.withVue;
var EmitterWrap = Emitter;
EmitterWrap.install = function (app) {
    withVue(app, 'Emitter', Emitter);
    return app;
};
export default EmitterWrap;
